
'use client';

import { useParams } from 'next/navigation';
import Link from 'next/link';
import React, { useEffect, useState } from 'react';
import { getMeterBySerialNumber, type SmartMeter } from '@/lib/sample-data';
import { Header } from '@/components/Header';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { Skeleton } from '@/components/ui/skeleton';
import { Mail, MessageSquare, User, MapPin, ArrowLeft } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { Badge } from '@/components/ui/badge';
import { sendNotification } from '@/ai/flows/send-notification';

interface BillingDetails {
  consumption: string;
  charges: { label: string; amount: string }[];
  totalAmount: string;
}

function calculateBilling(consumption: number, isSenior: boolean, isLifeline: boolean) {
  const rates = {
    generation: 5.73,
    transmission: 1.50,
    systemLoss: 0.30,
    distribution: 1.20,
    metering: 0.15,
    supply: 0.80,
    fitAllowance: 0.05,
    universal: 0.10,
  };

  const baseRate = rates.generation + rates.transmission + rates.systemLoss + rates.distribution + rates.metering + rates.supply;
  const baseAmount = baseRate * consumption;

  let lifelineDiscountAmount = 0;
  let seniorDiscountAmount = 0;
  let discountedAmount = baseAmount;

  // Apply lifeline discount first
  if (isLifeline && consumption <= 45) {
      lifelineDiscountAmount = discountedAmount * 0.20;
      discountedAmount -= lifelineDiscountAmount;
  }

  // Apply senior discount on the remaining amount
  if (isSenior && consumption <= 100) {
      seniorDiscountAmount = discountedAmount * 0.05;
      discountedAmount -= seniorDiscountAmount;
  }

  const vatAmount = discountedAmount * 0.12;
  const fitCharge = rates.fitAllowance * consumption;
  const universalCharge = rates.universal * consumption;
  const addOnCharges = fitCharge + universalCharge;

  const totalBill = discountedAmount + vatAmount + addOnCharges;

  const charges = [
      { label: 'Generation Charge', amount: (rates.generation * consumption).toFixed(2) },
      { label: 'Transmission Charge', amount: (rates.transmission * consumption).toFixed(2) },
      { label: 'System Loss Charge', amount: (rates.systemLoss * consumption).toFixed(2) },
      { label: 'Distribution Charge', amount: (rates.distribution * consumption).toFixed(2) },
      { label: 'Supply Charge', amount: (rates.supply * consumption).toFixed(2) },
      { label: 'Metering Charge', amount: (rates.metering * consumption).toFixed(2) },
  ];

  if (lifelineDiscountAmount > 0) {
      charges.push({ label: 'Lifeline Discount (20%)', amount: `-${lifelineDiscountAmount.toFixed(2)}` });
  }
  if (seniorDiscountAmount > 0) {
      charges.push({ label: 'Senior Citizen Discount (5%)', amount: `-${seniorDiscountAmount.toFixed(2)}` });
  }

  charges.push({ label: 'Value Added Tax (12%)', amount: vatAmount.toFixed(2) });
  charges.push({ label: 'FIT Allowance', amount: fitCharge.toFixed(2) });
  charges.push({ label: 'Universal Charge', amount: universalCharge.toFixed(2) });
  
  return {
      charges,
      totalAmount: totalBill.toFixed(2),
  };
}


export default function BillingPage() {
  const params = useParams();
  const { toast } = useToast();
  const [meter, setMeter] = useState<SmartMeter | null>(null);
  const [loading, setLoading] = useState(true);
  const [billingDetails, setBillingDetails] = useState<BillingDetails | null>(null);

  useEffect(() => {
    if (typeof params.id === 'string') {
      const meterData = getMeterBySerialNumber(params.id);
      setMeter(meterData || null);

      if (meterData) {
        // Calculate monthly consumption based on the meter's power reading.
        // We'll assume the peak power is used for an average of 4 hours per day over 30 days.
        const consumptionVal = meterData.power * 4 * 30;
        const bill = calculateBilling(consumptionVal, meterData.isSenior, meterData.isLifeline);

        setBillingDetails({
          consumption: consumptionVal.toFixed(2),
          charges: bill.charges,
          totalAmount: bill.totalAmount,
        });
      }
    }
    setLoading(false);
  }, [params.id]);
  
  const handleSend = async (method: 'SMS' | 'GMail') => {
    if (!meter || !billingDetails) return;

    let recipient: string;
    if (meter.name === 'Juan dela Cruz') {
      recipient = method === 'GMail' ? 'arenrutherford@gmail.com' : '09174113065';
    } else {
      // Use placeholder for other users
      recipient = method === 'GMail' 
        ? `${meter.name.toLowerCase().replace(/\s/g, '.')}@example.com` 
        : '09123456789';
    }

    const billingPeriod = new Date().toLocaleDateString('en-US', { year: 'numeric', month: 'long' });
    const subject = `Your SEMS Billing Statement for ${billingPeriod}`;
    
    const billItems = billingDetails.charges.map(c => `${c.label}: ₱ ${c.amount}`).join('\n');
    const body = `Dear ${meter.name},\n\nHere is your billing statement for meter ${meter.serialNumber}.\n\nBilling Period: ${billingPeriod}\nConsumption (kWh): ${billingDetails.consumption}\n\nBilling Breakdown:\n${billItems}\n\nTotal Amount Due: ₱ ${billingDetails.totalAmount}\n\nThank you,\nSEMS`.trim();

    toast({
      title: "Sending Notification...",
      description: `Preparing to send statement to ${recipient} via ${method}.`,
    });

    try {
      const result = await sendNotification({
        method,
        recipient,
        subject,
        body,
      });

      if (result.success) {
        toast({
          title: "Notification Sent",
          description: result.message,
        });
      } else {
        toast({
          variant: "destructive",
          title: "Error Sending Notification",
          description: result.message,
        });
      }
    } catch (error) {
      toast({
          variant: "destructive",
          title: "Failed to Send",
          description: "An error occurred while sending the notification.",
      });
    }
  }

  if (loading || !billingDetails) {
    return (
      <div className="flex min-h-screen w-full flex-col">
        <Header />
        <main className="flex flex-1 justify-center bg-muted/40 p-4 pt-12 md:p-8">
          <div className="w-full max-w-2xl space-y-6">
            <Skeleton className="h-10 w-44" />
            <Card className="w-full shadow-lg">
              <CardHeader>
                <Skeleton className="h-8 w-48" />
                <Skeleton className="h-4 w-32" />
                <Separator className="my-2" />
                <div className="pt-2 space-y-2">
                  <div className="flex items-center gap-3">
                    <Skeleton className="h-4 w-4 rounded-full" />
                    <Skeleton className="h-4 w-40" />
                  </div>
                  <div className="flex items-center gap-3">
                    <Skeleton className="h-4 w-4 rounded-full" />
                    <Skeleton className="h-4 w-64" />
                  </div>
                </div>
              </CardHeader>
              <CardContent className="grid gap-4">
                <div className="rounded-lg border bg-card p-4 space-y-4">
                  <div className="grid grid-cols-2 gap-x-4 gap-y-2">
                    <Skeleton className="h-5 w-32" />
                    <Skeleton className="h-5 w-24 justify-self-end" />
                    <Skeleton className="h-5 w-48" />
                    <Skeleton className="h-5 w-20 justify-self-end" />
                  </div>
                  <Separator />
                  <div>
                    <Skeleton className="h-5 w-40 mb-2" />
                    <div className="grid grid-cols-2 gap-x-4 gap-y-1">
                      <Skeleton className="h-5 w-48" />
                      <Skeleton className="h-5 w-24 justify-self-end" />
                      <Skeleton className="h-5 w-44" />
                      <Skeleton className="h-5 w-20 justify-self-end" />
                      <Skeleton className="h-5 w-32" />
                      <Skeleton className="h-5 w-20 justify-self-end" />
                      <Skeleton className="h-5 w-40" />
                      <Skeleton className="h-5 w-24 justify-self-end" />
                    </div>
                  </div>
                  <Separator />
                  <div className="grid grid-cols-2 gap-4">
                    <Skeleton className="h-6 w-40" />
                    <Skeleton className="h-6 w-24 justify-self-end" />
                  </div>
                </div>
              </CardContent>
              <CardFooter className="flex justify-end gap-2">
                  <Skeleton className="h-10 w-32" />
                  <Skeleton className="h-10 w-36" />
              </CardFooter>
            </Card>
          </div>
        </main>
      </div>
    );
  }

  if (!meter) {
    return (
      <div className="flex min-h-screen w-full flex-col">
        <Header />
        <main className="flex flex-1 items-center justify-center">
          <h1 className="text-2xl font-bold">Meter Not Found</h1>
        </main>
      </div>
    );
  }

  return (
    <div className="flex min-h-screen w-full flex-col">
      <Header />
      <main className="flex flex-1 justify-center bg-muted/40 p-4 pt-12 md:p-8">
        <div className="w-full max-w-2xl space-y-6">
          <Button asChild variant="outline" className="w-fit">
            <Link href="/dashboard">
              <ArrowLeft className="mr-2 h-4 w-4" />
              Back to Dashboard
            </Link>
          </Button>
          <Card className="w-full shadow-lg">
            <CardHeader>
              <CardTitle className="font-headline text-3xl">Billing Statement</CardTitle>
              <CardDescription>
                For meter {meter.serialNumber}
              </CardDescription>
              <Separator className="my-2" />
              <div className="flex items-center gap-3 pt-2">
                  <User className="h-4 w-4 text-muted-foreground"/>
                  <span className="font-semibold">{meter.name}</span>
                  {meter.isSenior && <Badge variant="secondary">Senior Citizen</Badge>}
                  {meter.isLifeline && <Badge variant="secondary">Lifeline</Badge>}
              </div>
              <div className="flex items-center gap-3">
                  <MapPin className="h-4 w-4 text-muted-foreground"/>
                  <span className="text-sm text-muted-foreground">{meter.address}</span>
              </div>
            </CardHeader>
            <CardContent className="grid gap-4">
              <div className="rounded-lg border bg-card p-4 space-y-4">
                <div className="grid grid-cols-2 gap-x-4 gap-y-2">
                  <div className="font-medium text-muted-foreground">Billing Period</div>
                  <div className="text-right font-mono">{new Date().toLocaleDateString('en-US', { year: 'numeric', month: 'long' })}</div>
                  <div className="font-medium text-muted-foreground">Consumption (kWh)</div>
                  <div className="text-right font-mono">{billingDetails.consumption}</div>
                </div>
                <Separator />
                <div>
                  <h4 className="font-medium mb-2">Billing Breakdown</h4>
                  <div className="grid grid-cols-2 gap-x-4 gap-y-1 text-sm text-muted-foreground">
                    {billingDetails.charges.map((charge) => (
                      <React.Fragment key={charge.label}>
                        <div>{charge.label}</div>
                        <div className="text-right font-mono">₱ {charge.amount}</div>
                      </React.Fragment>
                    ))}
                  </div>
                </div>
                <Separator />
                <div className="grid grid-cols-2 gap-4">
                  <div className="text-xl font-bold">Total Amount Due</div>
                  <div className="text-right text-xl font-bold font-mono">₱ {billingDetails.totalAmount}</div>
                </div>
              </div>
            </CardContent>
            <CardFooter className="flex justify-end gap-2">
              <Button variant="outline" onClick={() => handleSend('SMS')}>
                <MessageSquare className="mr-2 h-4 w-4" /> Send via SMS
              </Button>
              <Button onClick={() => handleSend('GMail')}>
                <Mail className="mr-2 h-4 w-4" /> Send via GMail
              </Button>
            </CardFooter>
          </Card>
        </div>
      </main>
    </div>
  );
}
